import React from 'react';
import { Card, Table, Tag, Tooltip, message, Button, Col, Row, Popconfirm, Radio, Input } from 'antd';
import {
    EyeOutlined, DeleteOutlined,
    DoubleRightOutlined, SendOutlined,
    EditOutlined, FileDoneOutlined,
    SearchOutlined
} from '@ant-design/icons';
import Highlighter from 'react-highlight-words';
import { connect } from 'react-redux';
import { useHistory } from 'react-router-dom';
import axios from 'axios';

const DealList = (props) => {
    let history = useHistory();
    let { user_id, role_id } = props;
    let [loading, setLoading] = React.useState(true);
    let [type, setType] = React.useState('Monthly');
    let [data, setData] = React.useState([]);

    const handleModeChange = (e) => {
        let mode = e.target.value
        setType(mode);
        setLoading(true);
        getDeals(mode)
    }

    const getDeals = async (type) => {
        try {
            let body = {};
            if (role_id == '1') {
                body = { user_id, role_id, type: type || 'Monthly' }
            } else {
                body = { user_id, role_id };
            }
            let res = await axios.post('/deallist', body);
            setData(res.data.data)
            setLoading(false);
        } catch (error) {
            setLoading(false);
            console.log(error);
        }
    }

    const deleteDeal = async (order_token) => {
        try {
            let res = await axios.post('/deletedeal', { user_id, order_token });
            message.success(res.data.message);
            getDeals()
        } catch (error) {
            console.log(error);
            error.response ?
                message.error(error.response.data)
                :
                message.error(error.message)
        }
    }

    const updateOrderStatus = async (order_token, orderstatus_id) => {
        try {
            await axios.post('/updateorderstatusagent', { user_id, order_token, orderstatus_id });
            getDeals();
        } catch (error) {
            console.log(error);
            error.response ?
                message.error(error.response.data)
                :
                message.error(error.message)
        }
    }

    //search work
    let searchInput = React.createRef();
    let [state, setState] = React.useState({});
    const getColumnSearchProps = dataIndex => ({
        filterDropdown: ({ setSelectedKeys, selectedKeys, confirm, clearFilters }) => (
            <div style={{ padding: 8 }}>
                <Input
                    ref={searchInput}
                    placeholder={`Search ${dataIndex}`}
                    value={selectedKeys[0]}
                    onChange={e => setSelectedKeys(e.target.value ? [e.target.value] : [])}
                    onPressEnter={() => handleSearch(selectedKeys, confirm, dataIndex)}
                    style={{ width: 188, marginBottom: 8, display: 'block' }}
                />
                <Button
                    type="primary"
                    onClick={() => handleSearch(selectedKeys, confirm, dataIndex)}
                    icon={<SearchOutlined />}
                    size="small"
                    style={{ width: 90, marginRight: 8 }}
                >
                    Search
            </Button>
                <Button onClick={() => handleReset(clearFilters)} size="small" style={{ width: 90 }}>
                    Reset
            </Button>
            </div>
        ),
        filterIcon: filtered => <SearchOutlined style={{ color: filtered ? '#1890ff' : undefined }} />,
        onFilter: (value, record) =>
            record[dataIndex]
                .toString()
                .toLowerCase()
                .includes(value.toLowerCase()),
        onFilterDropdownVisibleChange: visible => {
            if (visible) {
                setTimeout(() => searchInput.current?.select());
            }
        },
        render: text =>
            state.searchedColumn === dataIndex ? (
                <Highlighter
                    highlightStyle={{ backgroundColor: '#ffc069', padding: 0 }}
                    searchWords={[state.searchText]}
                    autoEscape
                    textToHighlight={text.toString()}
                />
            ) : (
                    text
                ),
    });

    const handleSearch = (selectedKeys, confirm, dataIndex) => {
        confirm();
        setState({
            ...state,
            searchText: selectedKeys[0],
            searchedColumn: dataIndex,
        });
    };

    const handleReset = clearFilters => {
        clearFilters();
        setState({ ...state, searchText: '' });
    };

    React.useEffect(() => {
        getDeals();
    }, []);

    const columns = [
        {
            title: 'Reference No.',
            dataIndex: 'order_token',
            key: 'order_token',
            align: 'center',
            render: text => <span>
                <Tag >
                    {text}
                </Tag>
            </span>,
            ...getColumnSearchProps('order_token'),
        },
        {
            title: 'Client Name',
            dataIndex: 'client_contactperson',
            key: 'client_contactperson',
            align: 'center',
            ...getColumnSearchProps('client_contactperson'),
        },
        {
            title: 'Client Email',
            dataIndex: 'client_email',
            key: 'client_email',
            align: 'center',
            ...getColumnSearchProps('client_email'),
        },
        {
            title: 'Created By',
            dataIndex: 'user_name',
            key: 'user_name',
            align: 'center',
            ...getColumnSearchProps('user_name'),
        },
        {
            title: 'Status',
            dataIndex: 'orderstatus_name',
            key: 'orderstatus_name',
            align: 'center',
            ...getColumnSearchProps('orderstatus_name'),
            render: (tag, record) => {
                let color = 'red';
                if (record.orderstatus_id == '1') color = 'blue'
                else if (record.orderstatus_id == '2') color = 'magenta'
                else if (record.orderstatus_id == '3') color = 'geekblue'
                else if (record.orderstatus_id == '4') color = 'purple'
                else if (record.orderstatus_id == '5') color = 'lime'
                else if (record.orderstatus_id == '6') color = '#f77474'
                else if (record.orderstatus_id == '7') color = 'blue'
                else if (record.orderstatus_id == '8') color = 'orange'
                else if (record.orderstatus_id == '9') color = 'gold'
                else if (record.orderstatus_id == '10') color = 'cyan'
                else if (record.orderstatus_id == '11') color = 'green'
                else if (record.orderstatus_id == '12') color = 'volcano'
                else if (record.orderstatus_id == '13') color = 'red'
                return (
                    <span>
                        <Tag color={color}>
                            {tag}
                        </Tag>
                    </span>
                )
            },
        },
        {
            title: 'Action',
            key: 'action',
            align: 'center',
            render: (text, record) => (
                <div>
                    <>
                        <Tooltip title="View">
                            <Button
                                type="primary"
                                className="mr-2"
                                icon={<EyeOutlined />}
                                size="small"
                                onClick={() => history.push(`/orders/${record.order_token}`)}
                            />
                        </Tooltip>
                    </>
                    {
                        (record.orderstatus_id == 1 && role_id == 4) &&
                        <Tooltip title="Forward To Manager">
                            <Button
                                type="primary"
                                className="mr-2"
                                icon={<DoubleRightOutlined />}
                                size="small"
                                onClick={() => updateOrderStatus(record.order_token, 2)}
                                style={{ background: 'orange', borderColor: 'orange' }}
                            />
                        </Tooltip>
                    }
                    {
                        ((record.orderstatus_id == 6 && role_id == 4) || (record.orderstatus_id == 5 && role_id == 3)) &&
                        <Tooltip title="Sent To Client">
                            <Button
                                type="primary"
                                className="mr-2"
                                icon={<SendOutlined />}
                                size="small"
                                onClick={() => updateOrderStatus(record.order_token, 7)}
                                style={{ background: 'orange', borderColor: 'orange' }}
                            />
                        </Tooltip>
                    }
                    {
                        record.orderstatus_id == 7 &&
                        <>
                            {/* <Tooltip title="View">
                                <Button
                                    type="primary"
                                    className="mr-2"
                                    icon={<EyeOutlined />}
                                    size="small"
                                    onClick={() => history.push(`/orders/${record.order_token}`)}
                                />
                            </Tooltip> */}
                            <Tooltip title="Forward To Billing">
                                <Button
                                    type="primary"
                                    style={{ background: '#1695f0', borderColor: '#1695f0' }}
                                    className="mr-2"
                                    icon={<FileDoneOutlined />}
                                    size="small"
                                    onClick={() => updateOrderStatus(record.order_token, 8)}
                                />
                            </Tooltip>
                        </>
                    }
                    {
                        record.orderstatus_id < 4 &&
                        <Popconfirm
                            placement="topRight"
                            title={'Are you sure, you want to delete this? '}
                            onConfirm={() => deleteDeal(record.order_token)}
                            okText="Yes"
                            cancelText="No"
                        >
                            <Tooltip title="Remove">
                                <Button danger icon={<DeleteOutlined />} size="small" className="mr-2" />
                            </Tooltip>
                        </Popconfirm>
                    }
                </div >
            )
        },
    ];

    //*********** MAIN RETURN ****************

    return (
        <>
            <Row style={{ justifyContent: 'center' }}>
                {
                    role_id == '1' &&
                    <Radio.Group onChange={handleModeChange} value={type} style={{ marginBottom: 8 }}>
                        <Radio.Button value="Monthly">Current Month Deal</Radio.Button>
                        <Radio.Button value="All">All Deals</Radio.Button>
                    </Radio.Group>
                }
                <Col lg={22}>
                    <Card bodyStyle={{ 'padding': '0px' }}>
                        <Table
                            columns={columns}
                            dataSource={data}
                            loading={loading}
                            rowKey='order_token'
                        />
                    </Card>
                </Col>
            </Row>
        </>
    )
}

const mapStateToProps = state => ({
    user_id: state.auth.user.user_id,
    role_id: state.auth.user.role_id,
});

export default connect(mapStateToProps)(DealList);